import OfferItem from './OfferItem';
import imgOffer1 from '../../images/offer-1.webp';
import imgOffer2 from '../../images/offer-2.webp';

const Offer = ( { footerRef } ) => {
	const items = [
		{
			id: 'o1',
			image: imgOffer1,
			title: '    ',
			description: '     '
		},
		{
			id: 'o2',
			image: imgOffer2,
			title: '    ',
			description: '       '
		}
	];

	return (
		<div className="f-offer">
			<div className="f-offer__title-container">
				<h2 className="f-offer__title f-heading-2"> </h2>
			</div>
			<div className="f-offer__items">
				{ items.map( ( item ) => (
					<OfferItem
						key={ item.id }
						image={ item.image }
						title={ item.title }
						description={ item.description }
						footerRef={ footerRef }
					/>
				) )
				}
			</div>
		</div>
	);
}

export default Offer;
