import DetailsItem from './DetailsItem';

const Details = () => {
	const items = [
		{
			id: 'd1',
			title: '',
			description: '<p>     :   !     -  .</p>'
		},
		{
			id: 'd2',
			title: '',
			description: '<p></p>'
		},
		{
			id: 'd3',
			title: '',
			description: '<p>  </p>'
		},
		{
			id: 'd4',
			title: ' ',
			description: '<ul><li>  12:0020:00</li><li>  10:0019:00</li></ul>'
		},
		{
			id: 'd5',
			title: '',
			description: '<p>,  ,  ,    ,  180</p>',
			button: { link: '#', name: ' ' }
		}
	]

	return (
		<div className="f-details">
			<div className="f-details__items">
				{ items && items.map( ( item ) => (
					<DetailsItem
						key={ item.id }
						title={ item.title }
						description={ item.description }
						button={ item.button }
					/>
				) )
				}
			</div>
		</div>
	);
}

export default Details;
