import FeedbackItem from './FeedbackItem';
import imgFrisson from '../../images/avatar_small.svg';
import imgUser from '../../images/user-avatar.png';

const Feedback = () => {

	return (
		<div className="f-feedback">
			<div className="f-feedback__title-container">
				<h2 className="f-feedback__title f-heading-2"></h2>
			</div>
			<div className="f-feedback__items flex fdc">
				<FeedbackItem
					image={imgFrisson}
					name='Frisson'
					date='4  '
					feedback=' !  ,     .    ,    . '
				/>
				<FeedbackItem
					image={imgUser}
					name=' '
					date='3  '
					feedback=',          ,       '
				/>
			</div>
			<div className="f-feedback__more-container flex">
				<span className="f-feedback__more">  16    Frisson.</span>
				<a href="#" className="f-feedback__more-link f-hover"></a>
			</div>
		</div>
	);
}

export default Feedback;
